<template>
  <AppBorder :app-info="ChatRoom" :size="size">
    <template v-slot:header>
      <div class="fx-ac">
        <button @click="connect"></button>
        <button @click="exit"></button>
<!--        <button @click="dl">dl</button>-->
        <div :class="self.status === 'onLine'?'onLine':'offline'"></div>
        <span class="name"
              @click="openDialog(MsgType.NICK_NAME)">{{ nickNameMap[self.clientName] || self.clientName }}</span>
      </div>
    </template>
    <template v-slot:main>
      <div class="flex main">
        <div class="left">
          <div class="fx-ver memberList">
            <div :class="`member fx-ac fx-pj ${self.curTalk === 'all' ? 'check':''}`" @click="groupChat">
          <span class="fx-ac">
          <HeadImg :item="groupImg"></HeadImg>
          <span class=" fz18"></span>
          </span>
              <span class="unread"
                    v-if="unreadMsgCountMap['all']">{{
                  unreadMsgCountMap['all'] > 0 ? unreadMsgCountMap['all'] : ''
                }}</span>
            </div>
            <div :class="`member fx-ac fx-pj ${self.curTalk === item ? 'check':''}`"
                 v-for="(item,idx) in memberList.list"
                 :key="idx" @click="privateChat(item)">
          <span class="fx-ac">
          <HeadImg :item="headImgMap[item]"></HeadImg>
          <span>{{ nickNameMap[item] || item }}</span>
          </span>
              <span class="unread"
                    v-if="unreadMsgCountMap[item]">{{
                  unreadMsgCountMap[item] > 0 ? unreadMsgCountMap[item] : ''
                }}</span>
            </div>
          </div>
        </div>
        <div class="right fx-ver">
          <div class="chatName">
            <div class="fx-ac" v-if="self.curTalk === 'all'">
              <HeadImg :item="groupImg" @click.native="openDialog(MsgType.GROUP_MSG)"></HeadImg>
              <span class=" fz22"></span>
            </div>
            <div class="fx-ac" v-if="self.curTalk !== 'all'">
              <HeadImg :item="headImgMap[self.curTalk]"></HeadImg>
              <span class="fz22">{{ nickNameMap[self.curTalk] || self.curTalk }}</span>
            </div>
          </div>
          <div class="top" id="talkBox">
            <div class="oneLine" v-for="(item,idx) in groupMsgList" :key="idx">
              <Message :item=item></Message>
            </div>
          </div>
          <div class="middle fx-ac ">
            <el-popover
                placement="top-start"
                width="300"
                trigger="hover">
              <div class="fx-bm emojiBox">
                  <span class="emoji fz18" v-for="(item,idx) in emojiList" :key="idx" @click="text+=item">{{
                      item
                    }}</span>
              </div>
              <span slot="reference" class="middleButton"></span>
            </el-popover>
            <span v-if="self.curTalk !== 'all'" class="fa fa-file-image-o fz22 funcButton"
                  @click="openDialog(MsgType.BINARY_IMG)" title=""></span>
            <span v-if="self.curTalk !== 'all'" class="fa fa-file-text fz22 funcButton"
                  @click="openDialog(MsgType.BINARY_FILE)" title=""></span>
          </div>
          <div class="down">
        <textarea v-model="text" class="textarea fz18" @keyup.enter="sendMsg">
        </textarea>
          </div>
        </div>
        <el-dialog
            title=""
            :visible.sync="showBox"
            width="30%"
            :modal="false">
          <label v-if="type === MsgType.NICK_NAME">
            <span></span>
            <input type="text" v-model="msg" maxlength="20" @keyup.enter="setNickname" class="textInput"/>
          </label>
          <label v-if="type === MsgType.HEAD_IMG || type === MsgType.GROUP_MSG">
            <div></div>
            <input type="text" v-model="msg" @keyup.enter="setHeadImg" class="textInput"/>
            <span class="tip" @click="search"></span>
          </label>
          <label v-if="type === MsgType.BINARY_IMG">
            <div>{{ fileName }}</div>
            <div class="drag-area fx-pac" @dragover="fileDragover" @drop="fileDrop">
              <img :src="previewUrl" alt="" width="100%" v-if="fileName">
              <div v-else class="uploader-tips">
                <span></span>
                <label for="fileInput" style="color: #11A8FF; cursor: pointer"></label>
              </div>
            </div>
          </label>
          <label v-if="type === MsgType.BINARY_FILE">
            <div></div>
            <div class="drag-area fx-pac" @dragover="fileDragover" @drop="fileDrop">
              <div v-if="fileName" class="file-name">{{ fileName }}</div>
              <div v-else class="uploader-tips">
                <span></span>
                <label for="fileInput" style="color: #11A8FF; cursor: pointer"></label>
              </div>
            </div>
          </label>
          <input id="fileInput" type="file" accept="image/*" style="display: none" @change="getFile">
          <span slot="footer" class="dialog-footer">
        <el-button @click="showBox = false"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </span>
        </el-dialog>
      </div>
    </template>
  </AppBorder>
</template>

<script>
import AppBorder from '../AppBorder'
import client from '../../utils/chatRoom/ConnectWebSocket'
import Message from './Message'
import HeadImg from "./HeadImg";
import pageManager from "@/utils/PageManager";
import MsgType from "@/utils/chatRoom/MsgType";

export default {
  name: "ChatRoom",
  components: {AppBorder, Message, HeadImg},
  computed: {
    tf() {
      return "matrix(" + this.transform.toString() + ")";
    }
  },
  data() {
    return {
      groupMsgList: client.getMessageList(),
      self: client.getSelf(),
      memberList: client.getMemberList(),
      nickNameMap: client.getNickNameMap(),
      headImgMap: client.getHeadImgMap(),
      privateMsgMap: client.getPrivateMsgMap(),
      unreadMsgCountMap: client.getUnreadMsgCountMap(),
      ChatRoom: pageManager.getAppInfo('ChatRoom'),
      MsgType: MsgType,
      text: "",
      talkBox: null,
      showBox: false,
      msg: '',
      type: '',
      groupImg: null,
      previewUrl: '',
      fileName: '',
      batchFile: null,
      emojiList: [''],
      size: {width: '975px', height: '705px'}
    }
  },
  created() {
    client.setChatRoom(this);
    this.emojiList = require('emojis-list');
  },
  mounted() {
    this.talkBox = document.getElementById('talkBox');
  },
  methods: {
    upload() {
      const file = document.getElementById("fileUP").files[0];
      if (!file) return;
      const formData = new FormData();
      formData.append("file", file);
      this.$axios({
        url: "http://localhost:8080/download",
        method: "post",
        data: formData
      }).then(response => {
        console.log(response.data);
      })
    },
    dl() {
      this.$axios({
        url: "http://localhost:9443/api/download",
        method: "get",
        responseType: "blob"
      }).then(response => {
            let blob = new Blob([response.data], {
              type: 'application/xml'
            });
            const link = document.createElement('a');
            link.href = window.URL.createObjectURL(blob);
            link.target = "_blank";
            // let filename = response.headers["content-disposition"].split(";")[1].split("filename=")[1];
            link.download = "pom.xml";//decodeURIComponent(filename);
            link.click();
            //
            window.URL.revokeObjectURL(link.href);
          }
      )
    },
    minimize() {
      pageManager.minimize("ChatRoom");
    },
    exit() {
      if (this.self.status === 'offline') {
        this.$message.error("");
        return;
      }
      client.close();
    },
    connect() {
      if (this.self.status === 'onLine') {
        this.$message.error("");
        return;
      }
      this.groupMsgList.length = 0;
      client.getConnection("chatroom");
      client.onmessage();
    },
    sendMsg() {
      if (client.client === null) {
        this.$message.error("");
        return;
      }
      let Msg = {
        msg: this.text,
        destination: "all",
        type: MsgType.TALK,
        sendTime: Date.now()
      }
      if (this.self.curTalk !== 'all') {
        Msg.type = MsgType.PRIVATE_CHAT;
        Msg.destination = this.self.curTalk;
        Msg.self = true;
        Msg.clientName = this.self.clientName;
        this.groupMsgList.push(Msg);
      }
      this.text = '';
      client.checkUnread(this, this.self.curTalk);
      if (this.self.clientName === Msg.destination) return;
      client.writeData(JSON.stringify(Msg));
    },
    privateChat(name) {
      this.self.curTalk = name;
      let msgList = this.privateMsgMap[name];
      if (!msgList) {
        this.$set(this.privateMsgMap, name, []);
        this.$set(this.unreadMsgCountMap, name, 0);
      }
      this.unreadMsgCountMap[name] = 0;
      this.groupMsgList = this.privateMsgMap[name];
      client.checkUnread(this, name);
    },
    groupChat() {
      this.self.curTalk = 'all';
      this.unreadMsgCountMap['all'] = 0;
      this.groupMsgList = client.getMessageList();
      client.checkUnread(this, name);
    },
    setNickname() {
      let Msg = {
        msg: this.msg,
        sendTime: Date.now(),
        type: MsgType.NICK_NAME
      }
      this.msg = '';
      client.writeData(JSON.stringify(Msg));
      this.showBox = false;
    },
    openDialog(type) {
      if (this.self.status === 'offline') {
        this.$message.error("");
        return;
      }
      this.type = type;
      this.showBox = true;
    },
    setHeadImg() {
      let Msg = {
        msg: this.msg,
        sendTime: Date.now(),
        type: this.type
      }
      this.msg = '';
      client.writeData(JSON.stringify(Msg));
      this.showBox = false;
    },
    sendImg() {
      let file = this.batchFile
      let Msg = {
        clientName: this.self.clientName,
        sendTime: Date.now(),
        type: MsgType.BINARY_IMG,
      }
      Msg.destination = this.self.curTalk;
      if (this.self.clientName !== Msg.destination)
        client.writeData(JSON.stringify(Msg), file);
      Msg.self = true;
      Msg.img = URL.createObjectURL(file);
      this.groupMsgList.push(Msg);
      this.showBox = false;
    },
    sendFile() {
      let file = this.batchFile
      let Msg = {
        msg: {
          fileName: this.fileName,
          totalSize: file.size,
          chunkSize: file.size,
          isAll: true
        },
        clientName: this.self.clientName,
        sendTime: Date.now(),
        type: MsgType.BINARY_FILE,
      }
      Msg.destination = this.self.curTalk;
      if (this.self.clientName !== Msg.destination)
        client.writeData(JSON.stringify(Msg), file);
      Msg.self = true;
      Msg.file = true;
      this.groupMsgList.push(Msg);
      this.showBox = false;
    },
    submit() {
      switch (this.type) {
        case MsgType.NICK_NAME:
          this.setNickname();
          break;
        case MsgType.HEAD_IMG:
        case MsgType.GROUP_MSG:
          this.setHeadImg();
          break;
        case MsgType.BINARY_IMG:
          this.sendImg();
          break;
        case MsgType.BINARY_FILE:
          this.sendFile();
          break;
      }
    },
    preview(file) {
      let suffix = file.name.split('.').pop();
      if (!['jpg', 'png', 'jpeg', 'gif'].includes(suffix)) {
        this.$message.error("");
        this.fileName = null;
        return;
      }
      if (file) this.previewUrl = URL.createObjectURL(file);
    },
    fileDragover(e) {
      e.preventDefault()
    },
    getFile() {
      let file = document.getElementById('fileInput').files[0];
      if (!file) return;
      this.batchFile = file;
      this.fileName = file.name;
      if (this.type === MsgType.BINARY_IMG) this.preview(file);
    },
    fileDrop(e) {
      e.preventDefault()
      const file = e.dataTransfer.files[0] // 
      if (!file) return
      if (file.size > 10 * 1024 * 1024) {
        return alert('10M')
      }
      this.batchFile = file;
      this.fileName = file.name;
      if (this.type === MsgType.BINARY_IMG) this.preview(file);
    },
    mousedown(ev) {
      pageManager.setFocusApp(this);
      this.positionOnclick[0] = this.transform[4];
      this.positionOnclick[1] = this.transform[5];
      this.positionOnclick[2] = ev.clientX;
      this.positionOnclick[3] = ev.clientY;
      this.mouseDown = true;
    },
    mouseup() {
      this.mouseDown = false;
    },
    closeApp() {
      pageManager.closeApp("ChatRoom")
    },
    search() {
      this.$emit('search');
    }
  }
}
</script>

<style scoped>
@import "../../assets/chatroom.css";
</style>